angular.module('player2').factory('UsuarioTipo', ['$http', function ($http) {
    return {
        get: function () {
            return $http.get('api/usuarioTipo');
        },

        save: function (usuarioTipo) {
            return $http({
                method: 'POST',
                url: 'api/usuarioTipo',
                data: $.param(usuarioTipo),
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                }
            });
        },

        edit: function (id) {
            return $http.get('api/usuarioTipo/' + id + '/edit');
        },

        update: function (dados) {
            return $http({
                method: 'PUT',
                url: 'api/usuarioTipo/' + dados.id,
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                },
                data: $.param(dados)
            });
        },

        destroy: function (id) {
            return $http.delete('api/usuarioTipo/' + id);
        }
    }
}]);
angular.module('player2').controller('UsuarioTipoController', ['$scope', '$rootScope', 'UsuarioTipo', function ($scope, $rootScope, UsuarioTipo) {

    $scope.usuarioTipo = {};

    $rootScope.loading = true;

    UsuarioTipo.get()
    .success(function(data) {
        $scope.usuarioTipos = data;
        $rootScope.loading = false;
    });

    $scope.create = function() {
        $scope.usuarioTipo = {};
        $scope.messages = null;
        $('#formModal').modal();
        $scope.tituloModal = 'messages.usuarioTipo_create';
        $scope.novoItem = true;
        $scope.formulario.$setPristine();
    }

    $scope.edit = function(id) {
        $rootScope.loading = true;
        UsuarioTipo.edit(id)
            .success(function(data) {
                $scope.usuarioTipo = data;
                $scope.messages = null;
                $('#formModal').modal();
                $scope.tituloModal = 'messages.usuarioTipo_edit';
                $scope.novoItem = false;
                $scope.formulario.$setPristine();
                $rootScope.loading = false;
        });
    };

    $scope.submit = function() {
        if($scope.novoItem) {
            this.save();
        } else {
            this.update();
        }
    };

    $scope.save = function() {
        $rootScope.loading = true;
        UsuarioTipo.save($scope.usuarioTipo)
                .success(function (data) {
                    UsuarioTipo.get()
                        .success(function (getData) {
                            $scope.usuarioTipos = getData;
                            $rootScope.loading = false;
                    });
                    $('#formModal').modal('hide');
                    $rootScope.loading = false;
                }).error(function(data, status) {
                    $scope.messages = data.errors;
                    $scope.status = status;
                    $rootScope.loading = false;
                });
    };

    $scope.update = function() {
        $rootScope.loading = true;
        UsuarioTipo.update($scope.usuarioTipo)
                .success(function (data) {
                    UsuarioTipo.get()
                        .success(function (getData) {
                            $scope.usuarioTipos = getData;
                            $rootScope.loading = false;
                    });
                    $('#formModal').modal('hide');
                    $rootScope.loading = false;
                }).error(function(data, status) {
                    $scope.message = data.errors;
                    $scope.status = status;
                    $rootScope.loading = false;
                });
    };

    $scope.delete = function(id) {
        $('#confirmaModal').modal();
        $scope.mensagemModal = 'messages.confirma_exclusao';
        $scope.idRegistro = id;
    };

    $scope.confirmacaoModal = function(id) {
        $rootScope.loading = true;
        UsuarioTipo.destroy(id)
            .success(function(data) {
                UsuarioTipo.get()
                    .success(function(data) {
                        $scope.usuarioTipos = data;
                        $rootScope.loading = false;
                });
                $('#confirmaModal').modal('hide');
                $rootScope.loading = false;
        });
    };

}]);

angular.module('player2').factory('Usuario', ['$http', function ($http) {
    return {
        show: function(id) {
            return $http.get('api/usuario/' + id);
        },

        get : function() {
            return $http.get('api/usuario');
        },

		save : function(usuario, arquivo) {
			if (typeof(arquivo)==='undefined') arquivo = null;
            return $http({
                method: 'POST',
                url: 'api/usuario',
                headers: { 'Content-Type' : undefined },
                transformRequest: function(data) {
                    var formData = new FormData();
                    angular.forEach(usuario, function(value, key) {
						formData.append(key, value);
					});
                    formData.append("imagem_perfil", arquivo);
                    return formData;
                }
            });
        },

        edit : function(id) {
            return $http.get('api/usuario/' + id + '/edit');
        },

		update : function(usuario, arquivo) {
			if (typeof(arquivo)==='undefined') arquivo = null;
            return $http({
                method: 'POST',
                url: 'api/usuario/' + usuario.id,
                headers: { 'Content-Type' : undefined },
                transformRequest: function(data) {
                    var formData = new FormData();
                    angular.forEach(usuario, function(value, key) {
						formData.append(key, value);
					});
                    formData.append("imagem_perfil", arquivo);
                    return formData;
                }
            });
        },

        destroy : function(id) {
            return $http.delete('api/usuario/' + id);
        },

        getCampeonatosInscritos : function(id) {
            return $http.get('api/campeonatosInscritosParaUsuario/' + id);
        },

        getCampeonatosDisponiveis : function(id) {
            return $http.get('api/campeonatosDisponiveisParaUsuario/' + id);
        },

		 getPartidas : function(id) {
		 	return $http.get('api/partidasParaUsuario/' + id);
	 	}
    }
}]);

