angular.module('player2').controller('PermissaoController', ['$scope', '$rootScope', 'Permissao', 'UsuarioTipo', 'Menu', function ($scope, $rootScope, Permissao, UsuarioTipo, Menu) {

    $scope.permissao = {};

    $rootScope.loading = true;

    UsuarioTipo.get()
    .success(function(data) {
        $scope.usuarioTipos = data;
        Menu.getTree()
        .success(function(dataMenu) {
            $scope.menus = dataMenu;
            $rootScope.loading = false;
        });
        $rootScope.loading = false;
    });

    $scope.getPermissoes = function() {
        Permissao.get($scope.permissao.usuario_tipos_id)
        .success(function(data) {
            $scope.permissoes = [];
            $scope.permissao.lista = {};
            angular.forEach(data, function(item) {
                $scope.permissoes.push(item.menu_id);
                $scope.permissao.lista[item.menu_id] = true;
            });
            $rootScope.loading = false;
        });
    };

    $scope.save = function() {
        Permissao.save($scope.permissao)
                .success(function (data) {
                    Permissao.get($scope.permissao.usuario_tipos_id)
                        .success(function (getData) {
                            $scope.permissoes = [];
                            $scope.permissao.lista = {};
                            angular.forEach(getData, function(item) {
                                $scope.permissoes.push(item.menu_id);
                                $scope.permissao.lista[item.menu_id] = true;
                            });
                            $rootScope.loading = false;
                    });
                    $rootScope.loading = false;
                }).error(function(data, status) {
                    $scope.messages = data.errors;
                    $scope.status = status;
                });
    };

    $scope.cancel = function() {
        $scope.getPermissoes();
    };

}]);

angular.module('player2').factory('Partida', ['$http', function ($http) {
	return {
		salvarPlacar: function (partida) {
			return $http({
				method: 'POST',
				url: 'api/partidas',
				data: $.param(partida),
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				}
			});
		},

		confirmarPlacar: function(dados) {
			return $http({
				method: 'PUT',
				url: 'api/partidas/' + dados.id_partida,
				data: $.param(dados),
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				}
			})
		},

		contestarResultado: function(dados, arquivo) {
			return $http({
				method: 'POST',
				url: 'api/partidas/contestar/' + dados.partidas_id,
				headers: { 'Content-Type' : undefined },
				transformRequest: function(data) {
					var formData = new FormData();
					formData.append("comentarios", dados.comentarios);
					formData.append("partidas_id", dados.partidas_id);
					formData.append("usuario_partidas_id", dados.usuario_partidas_id);
					formData.append("imagem", arquivo);
					return formData;
				},
				data: {comentarios: dados.comentarios, partidas_id: dados.partidas_id, usuario_partidas_id: dados.usuario_partidas_id, imagem: arquivo}
			});
		}
	}
}]);

angular.module('player2').controller('PartidaController', ['$scope', '$rootScope', '$filter', 'Campeonato', 'Usuario', 'Partida', '$state', '$timeout', function ($scope, $rootScope, $filter, Campeonato, Usuario, Partida, $state, $timeout) {

    $scope.campeonato = {};

    $scope.exibeDetalhes = false;

    $scope.files = [];

    $scope.$on("fileSelected", function (event, args) {
        $scope.$apply(function () {
            $scope.files.push(args.file);
        });
    });

    $scope.carregaPartidas = function (id_usuario) {
        $rootScope.loading = true;
        Usuario.getPartidas(id_usuario)
            .success(function (data) {
                $scope.partidas = data;
                $rootScope.loading = false;
            });
    };

    $scope.salvarPlacar = function (partida) {
        $rootScope.loading = true;

        partida.usuarioLogado = $rootScope.usuarioLogado;
        Partida.salvarPlacar(partida)
            .success(function () {
                $scope.carregaPartidas($rootScope.usuarioLogado);
                $rootScope.loading = false;
            })
            .error(function (data) {
                console.log(data.errors);
                $rootScope.loading = false;
            });

    }

    $scope.confirmarPlacar = function (id_partida) {
        $rootScope.loading = true;

        var dados = {};
        dados.id_partida = id_partida;
        dados.usuarioLogado = $rootScope.usuarioLogado;
        Partida.confirmarPlacar(dados)
            .success(function () {
                $scope.carregaPartidas($rootScope.usuarioLogado);
                $rootScope.loading = false;
            })
            .error(function (data) {
                console.log(data.errors);
                $rootScope.loading = false;
            });
    }

    $scope.contestarPlacar = function (id_partida) {
        $scope.contestacao_resultado = {};
        $scope.contestacao_resultado.partidas_id = id_partida;
        $scope.contestacao_resultado.usuario_partidas_id = $rootScope.usuarioLogado;
        $('#formModal').modal();
        $scope.tituloModal = 'messages.partida_contestar';
        $scope.formulario.$setPristine();
    }

    $scope.salvarContestacao = function () {
        console.log($scope.contestacao_resultado);
        Partida.contestarResultado($scope.contestacao_resultado, $scope.files[0])
            .success(function (data) {
                $scope.carregaPartidas($rootScope.usuarioLogado);
                $('#formModal').modal('hide');
                $scope.files = [];
                $rootScope.loading = false;
            }).error(function (data, status) {
                $scope.messages = data.errors;
                $scope.status = status;
                $rootScope.loading = false;
            });
    }

    $scope.exibeDataLimite = function (data_limite) {
        dataLimite = new Date(data_limite);
        return $filter('date')(dataLimite, 'dd/MM/yyyy HH:mm');
    }
}]);
